<template>
  <div class="Find">
    <van-nav-bar  fixed ref="navbar" :style="ys" >
      <template #left>
        <van-icon name="arrow-left" size="35" @click="go" />
      </template>
      <template #title>
        <span class="xx" v-show="iconOnly">朋友圈</span>
      </template>
      <template #right>
        <van-icon name="photograph" size="30" @click="pz"/>
      </template>
    </van-nav-bar>
    <van-image :src="require('@/assets/bj1.jpg')" class="bjt" ref="image">
      <div class="tx">
        <span class="mz">名字</span>
        <van-image :src="require('@/assets/images/sp1.jpg')" />
      </div>
    </van-image>

    <div class="dt">
      <div class="dtxq" v-for="item in dtlist" :key="item.id">
        <van-image :src="item.image" class="dttx" />
        <div class="nr">
          <div class="dtmz">{{ item.name }}</div>
          <span class="dtxx">{{ item.title }}</span>
          <van-image :src="item.image" class="dttp" />
          <van-cell class="time">
            <template #title>
              <span class="dttime">{{ item.time }}</span>
            </template>
            <template #right-icon>
              <transition name="dzpl">
                <div class="zandpl" v-if="item.id==mrz">
                <div class="dz" @click="dz">
                  <van-icon v-show="!dzzt" name="like-o" size="20"/>
                  <van-icon v-show="dzzt" name="like" size="20"/>
                  <span>赞</span>
                </div>
                <div class="dz">
                  <van-icon name="comment-o" size="20"/>
                  <span>评论</span>
                </div>
              </div>
              </transition>
              
              <van-image
                :src="require('@/assets/images/sl.png')"
                class="xzpl"
                @click="pl(item.id)"
              />
            </template>
          </van-cell>
          <div class="br"></div>
        </div>
        <van-divider />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      mrz:0,
      dtlist: [
        {
          id: 1,
          name: "稳定",
          title: "今天日常推按",
          image: require("@/assets/images/jy.png"),
          time: "3小时前",
        },
        {
          id: 2,
          name: "瓦达",
          title: "好心情",
          image: require("@/assets/images/sp1.jpg"),
          time: "3小时前",
        },
        {
          id: 3,
          name: "沙发",
          title: "今晚的月色",
          image: require("@/assets/images/sp2.jpg"),
          time: "4小时前",
        },
        {
          id: 4,
          name: "adawf",
          title: "时尚女装",
          image: require("@/assets/images/sp3.jpg"),
          time: "5小时前",
        },
        {
          id: 5,
          name: "稳定",
          title: "今天日常推按",
          image: require("@/assets/images/sp4.jpg"),
          time: "3小时前",
        },
      ],
      dzzt:false,
      iconOnly:false,
      ys:'',
    };
  },
  methods: {
    go() {
      // this.$router.go(-1);
      xm()
    },
    pz() {},
    showIconOnly(){
      this.iconOnly =false
      this.ys = {
        backgroundColor:'#fff',
        opacity: 0.2
      }
    },
    showFullNavbar(){
      this.iconOnly = true
      this.ys = {
        backgroundColor:'rgb(220, 220, 220)',
        opacity: 1
      }
    },
    dz(){
      this.dzzt = !this.dzzt;
    },
    pl(value) {
      if(this.mrz != value){
        this.mrz = value
      }else{
          this.mrz = 0
      }
    },
  },
  mounted(){
    const options = {
      root: null,
      rootMargin: "50px 50px",
      thresholds: 1,
    }
        const background = this.$refs.image.$el
        const io = new IntersectionObserver(entries => {
          
        const [entry] = entries 
        if (entry.isIntersecting) {
          this.showIconOnly()
        } else {
          this.showFullNavbar()
          //停止监听
          // io.unobserve(background)
        }
        },options)
        io.observe(background)
        }
};
</script>

<style lang="less" scoped>
	.dzpl-enter-active{
		animation: atguigu 0.5s linear;
	}

	.dzpl-leave-active{
		animation: atguigu 0.5s linear reverse;
	}

	@keyframes atguigu {
		from{
			transform: translateX(165px);
		}
		to{
			transform: translateX(0px);
		}
	}
.navbar {
  .xx {
    color: black;
    font-size: 20px;
  }
  .van-icon {
    margin-right: 10px;
  }
}
.van-nav-bar .van-icon {
  color: black;
}

/deep/.tx {
  height: 50px;
  text-align: end;
  margin-right: 20px;
  margin-top: -25px;
  .mz {
    position: relative;
    top: -30px;
    left: -10px;
    color: white;
  }
  .van-image {
    height: 50px;
    width: 50px;
  }
}
.dt {
  text-align: start;
  margin-bottom: 85px;
  margin-left: 10px;
  .dtxq {
    display: flex;
    flex-flow: row nowrap;
    margin: 0px 0 20px 0px;
  }
  .dttx {
    height: 50px;
    width: 50px !important;
    font-size: 14px;
    margin: 5px 0 ;
  }
  /deep/.van-image__img{
    border-radius: 5px;
  }
  .dtmz {
    font-size: 16px;
    color: slategray;
    

  }

  .dttp {
    max-width: 50vw;
    margin-top: 5px;
  }
  .nr {
    display: flex;
    flex-flow: column wrap;
    margin-left: 10px;
  }
  .xzpl {
    height: 20px;
  }
  .time {
    width: 70vw;
    height: 5vh;
    line-height: 5vh;
  }
  .zandpl{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    height: 30px;
    width: 40vw;
    line-height: 30px;
    background-color: gray;
    border-radius: 5px;
    margin-right: 10px;
    .van-icon{
      margin-right: 5px;
      margin-top: 5px;
    }
  }
  .br {
    height: 1px;
    background-color: #ccc;

  }
}
</style>
